header{
  height: 60px;
  width: 100%;
  padding: 0 10px 0 10px;
  background: #e35252;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .header__title{
    color: #334bcb;
    font-weight: bold;
    font-size: 20px;

  }
  img{
    cursor: pointer;
  }
  input{
    width: 35%;
    border-radius: 5px;
    border: none;
  }
}